<template>
  <div>
      <Head :title="detail.name" />
    <div class="imgbox">
      <img :src="detail.img">
    </div>

    <div id="detail">
      <div class="dname">
        <span class="price1">￥{{detail.price}}.00</span> 
        <span class="price2">￥{{detail.price}}8.00</span>  
        <span style="margin-left:60%" class="price3">包邮</span>
      </div>
      <p class="name">【新品】{{detail.name}}</p>
    </div>

 <van-cell-group>
      <van-cell  title="24小时发货 · 7天包退 · 售后补贴"   is-link/>
      <van-cell  :title="shopname1"   is-link  id="shop1" />
        <van-cell   :title="shopname" is-link  id="shop" />
    </van-cell-group>


    <van-tabs type="card">
  <van-tab title="详情">
    <van-notice-bar text="重要通知：包邮促销！！！包邮促销！！！包邮促销！！！" left-icon="volume-o"/>
   <img   class="showimg"    :src="detail.img">

  </van-tab>
  <van-tab title="评价">评价</van-tab>
  <van-tab title="售后">
      <van-cell  title="常用快递 ：中通快递"  />
       <van-cell  title="运费：全国包邮(偏远地区除外)"  />
  </van-tab>
</van-tabs>
 <!--  -->
 <van-goods-action>
  <van-goods-action-icon icon="chat-o" text="客服" @click="onClickIcon" />
  <van-goods-action-icon icon="cart-o" text="购物车" @click="onClickIcon"  :info="num"/>
  <van-goods-action-button type="warning" text="加入购物车" @click="onClickButton" />
  <van-goods-action-button type="danger" text="立即购买" @click="onClickButton" />
</van-goods-action>


<!-- 遮罩层 -->
<van-popup
 :round="true"
  v-model="show"
  position="bottom"
  :style="{ height: '45%' }"
>
<div class="top">
  <div class="leftimg">
    <img :src="detail.img" alt="">
  </div>
  <div class="rightbox">
    <p class="p1">￥{{detail.price}}.00</p>
    <p class="p2">{{shopname1}}</p>
  </div>
</div>

<van-icon name="close"   id="close"  size="30px"  @click="close()"/>
<van-divider />
<div class="countbox"><span>数量：</span><van-stepper v-model="value"   id="count"/></div>
<van-divider />
<van-button color="linear-gradient(to right, #ff8917,#ee0a24)" id="OK" @click="ok()">确定</van-button>

</van-popup>





 
</div>
</template>

<script>
import { Toast } from 'vant';
export default {
   data(){
        return {
          detail:[],
          shopname:"",
          shopname1:"",
          show:false,
          value:1,
          num:"",

          
    
          }
    },
    created(){
     var Id=this.$route.query.Id;
     window.console.log(Id);
     this.$axios.post("/vue/getGoodDetail",{params:{Id:Id}}).then(res=>{
         // window.console.log(res);
            this.detail = res.data.result;
           var sname=(this.detail.name).substring(0,4);
            this.shopname='店铺:'     +sname+'官方旗舰店'          
          this.shopname1='已选择:' +(this.detail.name).substring(0,23) +"....";
        });
    },
    mounted(){//获取购物车数量
      this.$axios.post("/vue/getShopcarInfo",{
            username:window.localStorage.getItem('users'),
        }).then(res=>{
            this.num = res.data.total;
        })

    },
    methods:{
      onClickIcon(){
        this.$router.push({name:"car"});
      },
      onClickButton(){
        this.show=true
      },
      close(){
        this.show=false;
      },
      ok(){
        this.$axios.post("/vue/addShopCar",{
                username:window.localStorage.getItem('users'),
                goodId:this.$route.query.Id,
                count:this.value,
                goodInfo:this.detail,
        }).then(res=>{
          //window.console.log(res.data);
          if(res.data.code==200){
            Toast("加入成功！")
          }else{
            Toast("加入失败，请重试")
          }

        })

        this.show=false;
      }




}

}


</script>



<style  lang="scss" scoped>
*{
   box-sizing:border-box 
}
.imgbox{
    width:100%;
    height:375px;
     overflow:hidden; 
    img{
        width:100%;
    height:375px;
   
    }
}

#detail{
  padding:5px 10px;
     width:100%;
    height:auto;
   
    p{
        text-align: left;
        font-size: 13px;
    color: #797d82;
    clear: both;
    }
    .dname{
    color: #191a1b;
    font-size: 18px;
    height: 35.5px;
    line-height: 35.5px;
    span{
      float: left;
      margin-right: 4px
    }
    .price1{
      font-size:24px;
      color: #ff464e;
    }
    .price2{
    font-size:12px;
    color: #bbbbbb;
    margin-right: 5px;
    text-decoration: line-through;
    }
    .price3{
      font-size:12px;
      width: 30px;
      height: 15px;
      line-height: 15px;
      background: #ff464e;
      color:#fff;
      margin-top: 10px;
      margin-left:0 !important;
    }    
    }

     .name{
      color: #333333;
      font-size: 16px;
     
    }

}

.van-cell{
  text-align:left;
  text-indent:5px;
  font-size:12px;
 
}

.showimg{
  width:100%;

}

#shop{
  margin-bottom:10px;

}

#shop1{
  width:100%;
overflow: hidden;
text-overflow:ellipsis;
white-space: nowrap;
}

// .van-tabs__nav--card{
//   border: 1px solid #666666 !important;
// }


// .van-tab{
//   color:#666 !important;
//   border-right: 0.5px solid #666 !important;
// }
// .van-tab--active{
//   background:#666 !important;
//   color:#fff !important;
// }

.top{
  width: 300px;
  height: 100px;
  margin:30px 30px;
  margin-bottom:10px;
  position: relative;
  .leftimg{
    width: 100px;
    height: 100px;
     float: left;
    img{
      width: 100%;
      height: 100%;
      float: left;
    }

  }

  .rightbox{
    width: 150px;
    height: 100px;
    float: left;
    margin-left:10px;
    text-align: left;
     .p1{
       margin:0  0 5px 0;
       font-size: 17px;
        color: #ff464e;
     } 
     .p2{
       font-size:15px;
     }

  }
}

#close{
  position: absolute;
  left:92%;
  top:5%;
}

.countbox{
  width: 100%;
  height: 30px;
  padding: 0 10px;
 
  display: flex;
  justify-content: space-between;
  span{
    font-size: 17px;
  }
}


#OK{
  width: 300px;
  border-radius: 0.533333rem  !important;
}
</style>
